.about {
  &__content {
    display: flex;
    flex-direction: column;
    gap: 32px;

    @include onTablet {
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 24px;
    }

    @include onDesktop {
      display: grid;
      grid-template-columns: 0.45fr 0.55fr 90px;
      column-gap: 24px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 48px;
    }

    @include onDesktop {
      font-size: 64px;
    }
  }

  &__discription {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;
  }
}
